.wrap {
    display: flex;
    flex-direction: column;
    background-color: teal;
    width: 75%;
    height: 100vh;
    overflow: hidden;

}
.wrap_todolists  {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
}
.todoList {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 0 auto;
}

.todoList > div {
    padding: 10px;
}


.todoList-task.done {
    opacity: 0.5;
}


.filter-active {
    background-color: blue;
}

.error {
    border-color: red;
}
